const $siteList = $('.siteList');
const $lastLi = $siteList.find('li.last');
const x = localStorage.getItem('x');//ËØªÂèñÊú¨Âú∞Â≠òÂÇ®
const xObject = JSON.parse(x);//ÂèòÊàêÂØπË±°
//Áî®parcel‰ºöÈªòËÆ§Âú®‰ª£Á†ÅÂ§ñÈù¢Âä†‰∏ÄÊõæ‰ΩúÁî®ÂüüÔºåÊâÄ‰ª•Ëøô‰∏™‰∏çÊòØÂÖ®Â±ÄÂèòÈáèÔºå‰∏çÁî®ÂΩìÂøÉÂÖ®Â±ÄÊ±°Êüì
const hashMap = xObject || [//Á¨¨‰∏ÄÊ¨°ÁöÑÊó∂ÂÄôxObjectÊòØÁ©∫ÁöÑÔºåÊâÄ‰ª•ËøôÈáåË¶ÅËÆæÁΩÆ‰∏Ä‰∏™ÂàùÂßãÂÄº
    {logo: 'G', logoType: 'text', url: 'https://github.com/'},
    {logo: 'J', logoType: 'text', url: 'https://juejin.im/'},
    {logo: 'V', logoType: 'text', url: 'https://vuejs.org/'},
    {logo: 'R', logoType: 'text', url: 'https://reactjs.org/'},
    {logo: 'I', logoType: 'text', url: 'https://www.iconfont.cn/'},
    {logo: 'B', logoType: 'text', url: 'https://www.bilibili.com/'},

    //Êñ∞Â¢ûÁΩëÁ´ôÔºö
];

const simplifyUrl = (url) => {
    //ÊòæÁ§∫ÂáΩÊï∞ÔºåÊääÂçèËÆÆÁúÅÁï•‰∏çÊòæÁ§∫
    return url.replace('https://', '')
        .replace('http://', '')
        .replace('www.', '')
        .replace(/\/.*/, '')//Â¶ÇÊûúËæìÂÖ•ÁöÑÁΩëÂùÄÂåÖÂê´Êü•ËØ¢Â≠óÁ¨¶‰∏≤
      .split('.')[0]

}
const render = () => {
    //Ê∏≤ÊüìÊ∑ªÂä†‰∫ÜÊàêÂëòÁöÑÊñ∞ÂìàÂ∏åË°®ÔºåË¶ÅÂéªÊéâ‰πãÂâçÁöÑÂ≠òÂú®ÁöÑÂ∑≤ÁªèÊ∏≤ÊüìÁöÑ
    $siteList.find('li:not(.last)').remove();
    hashMap.forEach((node, index) => {
        //Áî®jsÂÜôhtml
        const $li = $(`<li>
<!--            <a href="${node.url}">-->
                <div class="site">
                    <div class="logo">${node.logo}</div>
                    <div class="close">
                        <svg class="icon">
                            <use xlink:href="#icon-baseline-close-px"></use>
                        </svg>
                    </div>
                </div>
                <div class="link">${simplifyUrl(node.url)}</div>
<!--            </a>-->
        </li>`).insertBefore($lastLi);
        $li.on('click', ()=>{//‰∏çÁî®aÊ†áÁ≠æÔºåÁî®jsÂÆûÁé∞‰ª£ÊõøË∑≥ËΩ¨Âà∞Êñ∞È°µÈù¢ÔºåËøòÊòØË¶ÅÈòªÊ≠¢ÂÜíÊ≥°
            window.open(node.url);
        })
        $li.on('click', '.close', (e) => {
            e.stopPropagation();//ÈòªÊ≠¢ÂÜíÊ≥°ÁÇπÂáªËøòÊòØ‰ºöËß¶ÂèëaÊ†áÁ≠æË∑≥ËΩ¨Ôºå‰∏çÁî®aÊ†áÁ≠æ
            hashMap.splice(index, 1);
            render();//Âà†Èô§‰πãÂêéËÆ∞ÂæóÈáçÊñ∞Ê∏≤Êüì
        })

    })
}
//Â£∞Âêç‰∫ÜË¶Årender‰∏ÄÊ¨°
render();
$('.addButton')
    .on('click', () => {
        //ÈóÆÁî®Êà∑Â°´Âï•ÁöÑÂÖ®Â±ÄÊñπÊ≥ï
        let url = window.prompt('ËØ∑ËæìÂÖ•Âú∞ÂùÄ');
        if (!url)return
        if (url.indexOf('http') !== 0) {
            url = 'https://' + url;
        }

        // console.log(url);
        // console.log($siteList);
        // const $li = $(`
        //       <li>
        //     <a href="${url}">
        //         <div class="site">
        //             <div class="logo">${url[8]}</div>
        //             <div class="link">${url}</div>
        //         </div>
        //     </a>
        // </li>
        // `).insertBefore($lastLi);
        //Êñ∞Â¢ûÁΩëÂùÄ
        hashMap.push({logo: simplifyUrl(url)[0].toUpperCase(), logoType: 'text', url: url});
        render();
    })
//ÁÇπÂáªÈìæÊé•ÂÖ∂‰ªñÁΩëÁ´ô‰πãÂâçË¶ÅÊääÂΩìÂâçÁöÑÂìàÂ∏åÂ≠ò‰∏Ä‰∏ãÔºåÁ°Æ‰øùÈÄÄÂõûÂΩìÂâçÈ°µÁöÑÊó∂ÂÄô‰Ω†Ê∑ªÂä†ÁöÑÁΩëÂùÄËøòÂ≠òÂú®
//ÁõëÂê¨Á¶ªÂºÄÂΩìÂâçÈ°µÈù¢‰πãÂâçËß¶ÂèëÁöÑ‰∫ã‰ª∂Ôºö
//Â≠òÂà∞localStorage,storageÂè™ËÉΩÂ≠òÂÇ®Â≠óÁ¨¶‰∏≤
window.onbeforeunload = () => {
    // console.log("È°µÈù¢Ë¶ÅÂÖ≥Èó≠‰∫Ü");
    //ÂØπË±°ÂèòÊàêÂ≠óÁ¨¶‰∏≤
    const string = JSON.stringify(hashMap);
    localStorage.setItem('x', string);//key, value   ÊÑèÊÄùÊòØÂú®Êú¨Âú∞ÁöÑÂ≠òÂÇ®ÈáåÈù¢ËÆæÁΩÆ‰∏Ä‰∏™xÔºåxÁöÑÂÄºÂ∞±ÊòØstring

}

//ÁõëÂê¨ÈîÆÁõò‰∫ã‰ª∂
$(document).on('keypress', (e)=>{
    // const key=e.key;
    const {key} = e;//eÂØπË±°‰∏≠ÊúâkeyÂêåÂêç

    // for (let i=0; i<hashMap.length; i++){
    //     if (hashMap[i].logo.toLowerCase()===key){
    //         window.open(hashMap[i].url);
    //     }
    // }

    hashMap.forEach((node, index) =>{
        if (node.logo.toLowerCase()===key){
            window.open(node.url);
        }
    })
})